import { getAddress } from '@ethersproject/address'
import { arrayify, hexZeroPad, hexlify } from '@ethersproject/bytes'
import base58 from 'bs58'

export function hexZeroPadTo32(addr: string): string {
    return hexZeroPad(addr, 32)
}

export function bytes32ToEthAddress(bytes32: string | Uint8Array): string {
    if (bytes32 instanceof Uint8Array) {
        bytes32 = hexlify(bytes32)
    }
    return getAddress(bytes32.slice(-40))
}

export function trim0x(str: string): string {
    return str.replace(/^0x/, '')
}

/**
 * Convert address to bytes32
 * @param address 0x prefixed address(20bytes or 32bytes) or solana address
 */
export function addressToBytes32(address: string): Uint8Array {
    if (isSolanaAddress(address)) {
        return base58.decode(address)
    } else if (address.startsWith('0x') && address.length <= 66) {
        return arrayify(hexZeroPadTo32(address))
    }
    throw new Error('Invalid address')
}

const solanaAddressRegex = /^([1-9A-HJ-NP-Za-km-z]{32,44})$/

export function isSolanaAddress(address: string): boolean {
    return solanaAddressRegex.test(address)
}

const aptosAddressRegex = /^(0x)?[0-9a-fA-F]{64}$/
export function isAptosAddress(address: string): boolean {
    return aptosAddressRegex.test(address)
}

const initiaAddressRegex = /^0x[a-fA-F0-9]{40}$/
export function isInitiaAddress(address: string): boolean {
    return initiaAddressRegex.test(address)
}
